<template>
  <div>
    <div id="content">
      <h2>Calculs</h2>
      <div id="listImages" v-if="processInstances.length > 0">
          <md-theme md-name="table" md-theme="blue">
          <md-table>
            <md-table-header>
              <md-table-row>
               <md-table-head md-sort-by="process">Instance Id</md-table-head>
               <md-table-head>Process Definition</md-table-head>
               <md-table-head md-sort-by="item.startTime">Début</md-table-head>
               <md-table-head md-sort-by="item.startTime">Fin</md-table-head>
               <md-table-head>Initiateur</md-table-head>
               <md-table-head>Voir les résultats</md-table-head>
               <md-table-head>Variables</md-table-head>
               <md-table-head>Détail</md-table-head>
             </md-table-row>
             </md-table-header>
             <md-table-body>
               <md-table-row v-for="(item, index) in processInstances" :key="index">
                <md-table-cell>{{item.self.id}}</md-table-cell>
                <md-table-cell>{{item.self.processDefinitionKey}}</md-table-cell>
                <md-table-cell :key="index">{{moment(item.self.startTime).format('YYYY MM DD : h:mm:ss')}}</md-table-cell>
                <md-table-cell v-if="item.self.endTime !== null" :key="index">{{moment(item.self.endTime).format('YYYY MM DD : h:mm:ss')}}</md-table-cell>
                <md-table-cell v-if="item.self.endTime === null" :key="index">Calculs en cours</md-table-cell>
                <md-table-cell>{{item.self.startUserId}}</md-table-cell>
                <md-table-cell>
                  <router-link class="link" :to="'/result/' + item.self.id">voir les résultats</router-link>
                </md-table-cell>
                <md-table-cell>
                  <router-link v-if="item.self.id !== null" class="link" :to="'/variables/' + item.self.id">variables de {{item.self.id}}</router-link>
                </md-table-cell>
                <md-table-cell>
                  <router-link v-if="item.self.id !== null" class="link" :to="'/details/' + item.self.id">détails de {{item.self.id}}</router-link>
                </md-table-cell>
               </md-table-row>
             </md-table-body>
          </md-table>
          </md-theme>
      </div>
    </div>
  </div>
</template>

<script>
  import VueResource from 'vue-resource'
  let moment = require('moment')

  export default {
    name: 'Running',
    components: {
      VueResource
    },
    data () {
//      let parser = new DOMParser()
//      let xml = parser.parseFromString('', 'text/xml')
//      let xml = require('./test.cmof')
//      let viewer = new BpmnViewer({ container: '#process-status' })
//
//      viewer.importXML(xml, function (err) {
//        if (err) {
//          console.log('error rendering', err)
//        } else {
//          console.log('rendered')
//        }
//      })
      return {
        moment: moment,
        processInstances: []
      }
    },
    mounted: function () {
      this.getProcessInstances()
    },
    methods: {
      getProcessInstances () {
        this.$http.get(process.env.PROCESS_URL + ':' + process.env.PROCESS_PORT + '/rest/process/interferogramme-main/processInstance', {
          headers: {
            'Accept': 'application/json'
          }
        })
        .then((response) => {
          console.log(response)
          this.processInstances = response.data
        })
        .catch(function (data, status, request) {
          console.log(data)
        })
      }
    }
  }
</script>



