<template>
  <div id="app">
    <div v-if="userIsConnected">
      <div id="menu">
        <router-link class="link" to="/" exact>Choisir un calcul</router-link>
        <router-link class="link" to="/running">Calculs en cours</router-link>
        <router-link class="link" to="/done">Calculs terminés</router-link>
        <router-link class="link" to="/results">Résultats des calculs</router-link>
        <router-link class="link" to="/view">Visualisation du résultat</router-link>
        <span class="logging" v-bind:class="{ hide: isHide1 }" v-if="this.$session.exists()">
          Log as {{this.$session.get('email')}} <button name="button" v-on:click="logOut">Log out</button>
        </span>
        <span class="logging"  v-bind:class="{ hide: isHide2 }" v-else>
          <input id="emailAdress" class="mail" v-on:change="checkButtonAvailable" v-model="email" placeholder="Entrer votre email">
          <button name="button" v-on:click="logIn" :disabled="buttonDisabled">Log In</button>
        </span>

      </div>
      <div id="view">
        <div id="logo">
           <span id="logobig">
               <img id="logo-big" src='./assets/images/logo_big.png' />
           </span>
               <h4 class="title">Les <strong>déformations de la Terre</strong> vues du ciel</h4>
        </div>
        <div id="page">
           <router-view></router-view>
        </div>
      </div>
    </div>
    <div v-else>
      <div id="view">
        <div id="logo">
           <span id="logobig">
               <img id="logo-big" src='./assets/images/logo_big.png' />
           </span>
          <h4 class="title">Les <strong>déformations de la Terre</strong> vues du ciel</h4>
          <div class="loggingForm">
            Merci de vous connecter avant de commencer. <br />
            <input id="emailAdress" class="mail" v-on:change="checkButtonAvailable" v-model="email" placeholder="Entrer votre email">
            <button name="button" v-on:click="logIn" :disabled="buttonDisabled">Log In</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue'
  import VueSession from 'vue-session'

  Vue.use(VueSession)

  export default {
    name: 'app',
    data () {
      return {
        buttonDisabled: true,
        email: '',
        isHide1: false,
        isHide2: false,
        userIsConnected: ''
      }
    },
    created: function () {
      if (this.$session.exists()) {
        this.userIsConnected = true
      }
    },
    methods: {
      checkButtonAvailable: function () {
        if (this.email !== '') {
          this.buttonDisabled = false
        } else {
          this.buttonDisabled = true
        }
      },
      logIn: function () {
        if (this.checkEmail() !== true) {
          alert('Merci d\'entrer une adresse mail valide')
          return
        }
        this.$session.start()
        this.$session.set('email', this.email)
        this.isHide1 = false
        this.isHide2 = true
        this.userIsConnected = true
      },
      logOut: function () {
        this.$session.clear()
        this.$session.destroy()
        this.isHide1 = true
        this.isHide2 = false
        this.userIsConnected = false
      },
      checkEmail: function () {
        let re = /\S+@\S+\.\S+/
        return re.test(this.email)
      }
    }
  }
</script>

<style>

  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }

  body {
    font-family: 'Roboto', sans-serif;
  }

  #content {
    clear: both;
    width: 100%;
    height: 100%;
    display: inline;
  }

  #menu {
    position:fixed;
    overflow:auto;
    padding-bottom: 15px;
    padding-top: 15px;
    border-color: #dddd;
    border-bottom-style: solid;
    border-width: 1px;
    right:0px;
    left:0px;
    top:0px;
    /*height:80px;*/
    /*border:solid 1px;*/
    background-color: white;
    z-index: 9999;
    /*font-size:90%;*/
  }

  a.link {
    color: #1c1c1c;
    text-decoration: none;
    font-size: 0.8rem;
    letter-spacing: 1px;
    text-transform: uppercase;
    font-weight: bold;
    font-family: 'Roboto', sans-serif;
  }

  .link.router-link-active{
    border-bottom: 2px solid orange;
    padding-bottom: 15px;
  }

  .link{
    #float: right;
    border-bottom: none;
    display: inline;
    list-style: none;
    margin: 10px;
    cursor: pointer;
    font-family: inherit;
    font-size: 1rem;
    line-height: 1.6;
    list-style-position: outside;
    margin-bottom: 1.25rem;
  }
  #page {
    position: absolute;
    top: 350px;
    height: 600px;
    width: 100%;
  }

  h4.title{
    text-transform: uppercase;
    font-family: 'Roboto', sans-serif;
    color: #444;
    font-size: 1.4375rem;
    line-height: 1.4;
    text-align: center;
  }

  h4.title strong{
    font-weight: bold;
    color: #3584bc;
  }

  img#logobig, img#logo-big{
     width: 600px;
     height: 210px;
   }

  span#logobig, span#logo-big{
    display: block;
    text-align: center;
    margin-top: 50px;
  }

  .logging {
    float: right;
  }

  .hide {
    display: none;
  }

  .loggingForm {
    text-align: center;
    width: 100%;
  }

</style>



