<template>

  <div>
    <div id="content">
      <div id="form">
        <div id="search">
          <h3>Résultat interferogramme</h3>
          <br/>
          <br/>
          <div style="clear:both" />
          <span style="width:100px;float:left" >Email : </span>
          <br/>
          <input id="emailAdress" class="mail" v-model="email" placeholder="Enter your email" disabled="disabled">
          <span style="float:left;clear:both" />
          <br/>
          <span style="width:100px;float:left;">Images PEPS : </span>
          <br/>
          <div v-for="(item, index) in pepsIds" :key="item.id">
            <input id="emailAdress" class="mail" :v-model="item" :placeholder="item" disabled="disabled">
          </div>
          <span style="clear:both;float:left" />
          <md-theme md-name="table" md-theme="blue">
          <md-table>
            <md-table-header>
              <md-table-row>
               <md-table-head md-sort-by="process">Webservice</md-table-head>
               <md-table-head>Etat</md-table-head>
             </md-table-row>
             </md-table-header>
             <md-table-body>
               <md-table-row v-for="(item, index) in wsStatus" :key="item.id">
                <md-table-cell>WS{{index}}</md-table-cell>
                <md-table-cell v-if="item !== undefined">{{item.StatusInfo.Status}}</md-table-cell>
               </md-table-row>
             </md-table-body>
          </md-table>
          </md-theme>
          <br/>
          <br/>
        </div>
      </div>
      <div id="mapArea">
        <!--<button name="button" v-on:click="switchMapStyle">Switch map style</button>-->
        <v-map style="height: 100%" :zoom="zoom" :center="center">
          <v-tilelayer :url="url" :attribution="attribution"></v-tilelayer>
          <v-marker v-for="item in markers" :key="item.id" :lat-lng="item.position" :visible="item.visible" :draggable="item.draggable"
                    v-on:l-move="markerMoved($event, item)" :icon="img">
            <v-popup :content="item.tooltip"></v-popup>
            <v-tooltip :content="item.tooltip"></v-tooltip>
          </v-marker>
          <v-polygon v-for="item in displayedImages" :key="item.id" :lat-lngs="item.geometry.coordinates" :color="imagesColor"></v-polygon>
        </v-map>
      </div>
      <div id="listImages" v-if="wsResults.length > 0">
        <br />
        <md-theme md-name="table" md-theme="blue">
          <md-table>
            <md-table-header>
              <md-table-row>
               <md-table-head md-sort-by="process">Sous-Fauchée</md-table-head>
               <md-table-head>Vignette</md-table-head>
               <md-table-head>Image</md-table-head>
               <md-table-head>Interferogramme</md-table-head>
             </md-table-row>
             </md-table-header>
             <md-table-body>
               <md-table-row v-for="(item, index) in wsResults" :key="item.id">
                <md-table-cell>{{item.subSwath}}</md-table-cell>
                <md-table-cell><a class="link" :href="item.resNames[0].resURI + '.jpg'">vignette {{item.subSwath}}</a></md-table-cell>
                <md-table-cell><a class="link" :href="item.resNames[1].resURI + '.jpg'">image {{item.subSwath}}</a></md-table-cell>
                <md-table-cell><a class="link" :href="item.resNames[1].resURI + '.int'">interferogramme {{item.subSwath}}</a></md-table-cell>
              </md-table-row>
             </md-table-body>
          </md-table>
          </md-theme>
      </div>
      <div id="listImages" v-else>
        Calculs en cours.
      </div>
    </div>
  </div>


</template>

<script>

  import Vue2Leaflet from 'vue2-leaflet'
  import L from 'leaflet'
  import VueResource from 'vue-resource'
  import Vue from 'vue'
  let jsonPath = require('jsonpath')
  require('leaflet-draw')

  let markerList = []
  let circleList = []
  let mapId = 1
  let icon = new L.Icon({
    iconUrl: require('../assets/images/marker-icon.png'),
    iconRetinaUrl: require('../assets/images/marker-icon-2x.png'),
    shadowUrl: require('../assets/images/marker-shadow.png'),
    iconSize: [25, 41],
    iconAnchor: [12, 41],
    popupAnchor: [1, -34],
    tooltipAnchor: [16, -28],
    shadowSize: [41, 41]
  })

  Vue.use(VueResource)

  export default {
    name: 'Start',
    components: {
      VueResource,
      'v-map': Vue2Leaflet.Map,
      'v-tilelayer': Vue2Leaflet.TileLayer,
      'v-geojson-layer': Vue2Leaflet.GeoJSON,
      'v-marker': Vue2Leaflet.Marker,
      'v-circle': Vue2Leaflet.LCircle,
      'v-tooltip': Vue2Leaflet.Tooltip,
      'v-popup': Vue2Leaflet.Popup,
      'v-rectangle': Vue2Leaflet.Rectangle,
      'v-polygon': Vue2Leaflet.Polygon
    },
    data () {
      return {
        zoom: 2,
        center: [47.413220, -1.219482],
        url: 'http://{s}.tile.opentopomap.org/{z}/{x}/{y}.png',
        attribution: '&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors',
        markers: markerList,
        circles: circleList,
        option: {
          format: 'YYYY-MM-DD'
        },
        id: '',
        pepsIds: [],
        images: [],
        wsStatus: [],
        wsResults: '',
        displayedImages: [],
        email: '',
        areaMarkerTooltip: 'Selected area',
        path: '/assets/images/',
        img: icon,
        mapObject: [],
        imagesColor: 'green'
      }
    },
    mounted: function () {
      console.log(this)
      this.id = this.$route.params.id
      this.mapObject = this.$children[1].mapObject
      var drawnItems = new L.FeatureGroup()
      this.mapObject.addLayer(drawnItems)
      var drawControl = new L.Control.Draw({
        draw: {
          rectangle: {
            shapeOptions: {
              color: '#FFFF00'
            }
          },
          marker: false,
          circle: false,
          polygon: false,
          polyline: false
        },
        edit: {
          featureGroup: drawnItems
        }
      })
      this.mapObject.addControl(drawControl)

      this.mapObject.on(L.Draw.Event.CREATED, function (e) {
        let layer = e.layer
        let bounds = e.layer.getBounds()
        let newArea = {'bounds': [[bounds.getNorthEast().lat, bounds.getNorthEast().lng], [bounds.getSouthWest().lat, bounds.getSouthWest().lng]], 'color': 'red'}
        this.rectangle = newArea
        drawnItems.clearLayers()
        drawnItems.addLayer(layer)
      })
      this.getVariablesInstances()
    },
    methods: {
      switchMapStyle () {
        if (mapId === 1) {
          console.log('Switch to Word_Topo_Map')
          this.url = 'http://server.arcgisonline.com/ArcGIS/rest/services/World_Topo_Map/MapServer/tile/{z}/{y}/{x}'
          mapId = 2
        } else {
          console.log('Switch to opentopomap')
          this.url = 'http://{s}.tile.opentopomap.org/{z}/{x}/{y}.png'
          mapId = 1
        }
      },
      getProcessInstance () {
        let request = 'http://localhost:8180/rest/processInstance/' + this.id
        this.$http.get(request, {
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'processDefinitionKey': 'interferogramme-main'
          }
        })
        .then((response) => {
          console.log(response)
        })
        .catch(function (data, status, request) {
          console.log(data)
        })
      },
      getVariablesInstances () {
        let request = process.env.PROCESS_URL + ':' + process.env.PROCESS_PORT + '/rest/processInstance/' + this.id + '/variable'
        this.$http.get(request, {
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'processDefinitionKey': 'interferogramme-main'
          }
        })
        .then((response) => {
          this.pepsIds = jsonPath.query(response.body, '$[?(@.name=="images")].value')[0]
          this.email = jsonPath.query(response.body, '$[?(@.name=="email")].value')
          for (let i = 0; i < 8; i++) {
            let ws = jsonPath.query(response.body, '$[?(@.name=="ws' + i + 'Response")].value')[0]
            if (ws !== undefined) {
              this.wsStatus.push(ws)
              console.log(ws)
            }
          }
          let result = jsonPath.query(response.body, '$[?(@.name=="ws7Results")].value')
          result = result[0]
          let vars = []
          let keys = Object.keys(result.results)
          keys.forEach(function (element, index, array) {
            let tempVar = result.results[index]
            let key = parseInt(element) + 1
            tempVar.subSwath = key
            vars.push(tempVar)
          })
          this.wsResults = vars
          console.log(response)
        })
        .catch(function (data, status, request) {
          console.log(data)
        })
      }

    }
  }
</script>

<style scoped>
  @import "../../node_modules/leaflet-draw/dist/leaflet.draw.css";
  @import "../../node_modules/leaflet/dist/leaflet.css";

  .leaflet-draw-toolbar a {
    background-image: url('/static/spritesheet.png');
  }

  #mapArea {
    display: inline-block;
    position: absolute;
    width: 58%;
    height: 100%;
    float: right;
    right: 0;
  }

  select{
   width: 90%;
  }

  input.mail{
     width: 87%;
  }

  #form {
    display: inline-block;
    position: relative;
    left: 0.5%;
    width: 40%;
    height: 100%;
    background-color: #f0f0f0;
    float: left;
  }

  .thumbnail{
      position: relative;
      width: 75px;
      height: 75px;
      overflow: hidden;
      cursor: pointer;
      background-color: #292929;

  }

  .th{
      border: solid 4px #fff;
      box-shadow: 0 0 0 1px rgba(0,0,0,0.2);
      display: inline-block;
      line-height: 0;
      max-width: 100%;
      top: 4px;
      transition: all 200ms ease-out;
  }

  .text-center{
  text-align: center !important;
  }

  div#results{
    position: relative;
    border: 1px solid #ccc;
    margin: 5px;
    clear:both;
  }

  .columns{
      padding-left: 1.9375rem;
      padding-right: 1.9375rem;
      float: left;
      position: relative;

  }

  .large-3 {
      width: 20%;
  }

  .large-4 {
    text-align:center;
  }

  .light{
      color: gray;
  }

  #launchProcess{
    text-align: center;
    position: absolute;
    top: 770px;
    left: 46%;
    width: 30%;
    background-color: #f0f0f0;
  }

  #moreImages {
    text-align: center;
    position: absolute;
    padding-bottom: 15px;
    margin-top: 30px;
    left: 40%;
    width: 20%;
    background-color: #f0f0f0;
  }

  #emailAdress {
    width: 55%;
  }

  #listImages {
      height: 600px;
      width: 100%;
      top: 850px;
      position: absolute;

  }
</style>



