import Vue from 'vue'
import Router from 'vue-router'
import ListProcess from '@/components/ListProcess'
import Start from '@/components/Start'
import Running from '@/components/Running'
import Done from '@/components/Done'
import Details from '@/components/Details'
import Variables from '@/components/Variables'
import Task from '@/components/Task'
import Results from '@/components/Results'
import ResultInterferogramme from '@/components/ResultInterferogramme'
import InterferogrammeViewer from '@/components/InterferogrammeViewer'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'ListProcess',
      component: ListProcess
    }, {
      path: '/interferogramme',
      name: 'Start',
      component: Start
    }, {
      path: '/running',
      name: 'Running',
      component: Running
    }, {
      path: '/done',
      name: 'Done',
      component: Done
    }, {
      path: '/details/:id',
      name: 'Details',
      component: Details
    }, {
      path: '/variables/:id',
      name: 'Variables',
      component: Variables
    }, {
      path: '/task/:id',
      name: 'Task',
      component: Task
    }, {
      path: '/results',
      name: 'Results',
      component: Results
    }, {
      path: '/result/:id',
      name: 'ResultInterferogramme',
      component: ResultInterferogramme
    },
    {
      path: '/view',
      name: 'InterferogrammeViewer',
      component: InterferogrammeViewer
    }
  ]
})



