/**
 * ben quoi
 */
<template>
 <span class="double-range" @mousemove="drag" :class="disabled ? 'disabled' : ''">
   <div class="range-value range-min">{{min}}</div>
   <div class="range-value range-max">{{max}}</div>
   <div class="barre">
      <!--  <div class="inside-range"></div>-->
       <canvas id="colorScaleLegend" width="256" height="1" ></canvas>
       <div id="begin-slider" class="range-slider"  @mousedown="dragStart"></div>
       <div class="step-value step-begin" v-show="displayed.min">{{Math.round(displayed.min)}}</div>
    <div id="end-slider" class="range-slider"  @mousedown="dragStart"></div>
    <div class="step-value step-end" v-show="displayed.max">{{Math.round(displayed.max)}}</div>
   </div>
 </span>
</template>
<script>
var plotty = require('plotty')

export default {
  name: 'FormaterDoubleRange',
  props: {
    min: {
      type: Number,
      default: -100
    },
    max: {
      type: Number,
      default: 100
    },
    width: {
      type: Number,
      default: 200
    },
    defaultRange: {
      type: Object,
      default: null
    },
    colorScale: {
      type: String,
      default: 'viridis'
    },
    disabled: {
      type: Boolean,
      default: true
    }
  },
  watch: {
    defaultRange (newvalue, oldvalue) {
      this.placeSliders()
    },
    colorScale (newvalue) {
      this.initColorScale()
    }
  },
  data () {
    return {
      displayed: {
        min: null,
        max: null
      },
      mouseupListener: null,
      selectedSlider: null,
      w0: 10
    }
  },
  computed: {
    rate () {
      var barre = this.$el.querySelector('#colorScaleLegend')
      var rate = (barre.offsetWidth + 2) / (this.max - this.min)
      return rate
    }
  },
  created () {
    this.mouseupListener = this.dragEnd.bind(this)
    document.addEventListener('mouseup', this.mouseupListener)
  },
  mounted () {
    this.$el.querySelector('.barre').style.width = this.width + 'px'
    this.w0 = this.$el.querySelector('.range-slider').offsetWidth
    this.placeSliders()
    this.initColorScale()
  },
  destroyed () {
    document.removeEventListener('mouseup', this.mouseupListener)
    this.mouseupListener = null
  },
  methods: {
    initColorScale: function () {
      var canvas = this.$el.querySelector('#colorScaleLegend')
      plotty.renderColorScaleToCanvas(this.colorScale, canvas)
    },
    // placeSliders when defaultRange change
    placeSliders () {
      if (this.defaultRange) {
        this.displayed = this.defaultRange
      }
      var begin = this.$el.querySelector('#begin-slider')
      var end = this.$el.querySelector('#end-slider')
      var inside = this.$el.querySelector('#colorScaleLegend')
      this.posL = Math.round((this.displayed.min - this.min) * this.rate)
      begin.style.left = (this.posL - this.w0) + 'px'
      this.posR = Math.ceil((this.displayed.max - this.min) * this.rate)
      end.style.left = this.posR + 'px'
      inside.style.left = this.posL + 'px'
      inside.style.width = (this.posR - this.posL) + 'px'
      this.$el.querySelector('.step-value.step-begin').style.left = (this.posL - this.w0 / 2 - 15) + 'px'
      this.$el.querySelector('.step-value.step-end').style.left = (this.posR - 15 + this.w0 / 2) + 'px'
    },
    dragStart (event) {
      this.selectedSlider = event.target
    },
    drag (event) {
      if (!this.selectedSlider) {
        return
      }
      var position = this.$el.querySelector('.barre').getBoundingClientRect()
      switch (this.selectedSlider.id) {
        case 'begin-slider':
          this.dragBeginSlider(event.clientX, position.left)
          break
        case 'end-slider':
          this.dragEndSlider(event.clientX, position.left)
          break
      }
    },
    dragBeginSlider: function (x, left) {
      var posL = Math.round(x - left + this.w0 / 2)
      if ((posL > -this.w0 / 2) && (posL - this.w0 / 2) < this.posR) {
        this.posL = posL
        this.$el.querySelector('#begin-slider').style.left = (this.posL - this.w0) + 'px'
        this.$el.querySelector('.step-value.step-begin').style.left = (this.posL - this.w0 / 2 - 15) + 'px'
        this.displayed.min = Math.round(this.posL / this.rate + this.min)
        var inside = this.$el.querySelector('#colorScaleLegend')
        inside.style.left = this.posL + 'px'
        inside.style.width = (this.posR - this.posL) + 'px'
      }
    },
    dragEndSlider: function (x, left) {
      var posR = Math.round(x - left)
      if ((posR < this.$el.querySelector('.barre').offsetWidth) && (posR > this.posL)) {
        this.posR = posR
        this.$el.querySelector('#end-slider').style.left = this.posR + 'px'
        this.$el.querySelector('.step-value.step-end').style.left = (this.posR - 15 + this.w0 / 2) + 'px'
        this.displayed.max = Math.round(this.posR / this.rate + this.min)
        var inside = this.$el.querySelector('#colorScaleLegend')
        inside.style.left = this.posL + 'px'
        inside.style.width = (this.posR - this.posL) + 'px'
      }
    },
    dragEnd (event) {
      this.selectedSlider = null
      this.emitInput()
    },
    emitInput () {
      this.$emit('change', this.displayed)
    }
  }
}
</script>
<style>

.double-range .barre{
   position: relative;
   margin-left:20px;
   margin-right:20px;
   padding: 12px 15px;
   clear:both;
   opacity:1;
    /* Styles */
   background: #fff;
   border-radius: 5px;
   border: 1px solid grey;
   box-shadow: 0 1px 0 rgba(0,0,0,0.2);
   outline: none;
}
.double-range.disabled{
	opacity:0.8;
}
.double-range .inside-range,
#colorScaleLegend{
    position:absolute;
    background: green;
    height:100%;
    top:0;
    left:0;
    width:100%;
}
.barre .range-slider{
    position:absolute;
    width:10px;
    top:-3px;
    bottom:-3px;
    background: #b5bdc8; /* Old browsers */
    background: -moz-linear-gradient(top, #b5bdc8 0%, #828c95 71%, #28343b 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(top, #b5bdc8 0%,#828c95 71%,#28343b 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, #b5bdc8 0%,#828c95 71%,#28343b 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    border: 1px solid rgba(0,0,0,0.6);
    box-shadow: 0 1px 0 rgba(0,0,0,0.2);
    pointer-events:auto;
    cursor:pointer;
}
.double-range.disabled .range-slider {
	cursor:none;
	pointer-events:none;
}
[id="begin-slider"]{
    left:-10px;
}
[id="end-slider"]{
    right:-10px;
}
.barre .step-value{
    position: absolute;
    font-size:0.8rem;
    font-weight:900;
    width: 30px;
    text-align: center;
    top:-22px;
    height:20px;
    background: rgba(240, 240, 240, 0.5)   
}
.range-value{
    font-size:0.8em;
    width: 40px;
    text-align:center;
}
.range-value.range-min{
    float:left;
}
.range-value.range-max{
    float:right;
}
</style>



