<template>
  <div>
    <div id="content">
      <h2>Détails de l'instance {{id}}</h2>
      <div id="listImages" v-if="activities.length > 0">
          <md-theme md-name="table" md-theme="blue">
          <md-table>
            <md-table-header>
              <md-table-row>
               <md-table-head md-sort-by="process">Activity Id</md-table-head>
               <md-table-head>Activity Name</md-table-head>
               <md-table-head>Activity Type</md-table-head>
               <md-table-head md-sort-by="item.startTime">Début</md-table-head>
               <md-table-head md-sort-by="item.startTime">Fin</md-table-head>
               <md-table-head>Durée</md-table-head>
               <md-table-head>Process Appelé</md-table-head>
               <md-table-head>Tâche</md-table-head>
               <md-table-head>Assigné</md-table-head>
             </md-table-row>
             </md-table-header>
             <md-table-body>
               <md-table-row v-for="(item, index) in activities" :key="index">
                <md-table-cell>{{item.id}}</md-table-cell>
                <md-table-cell>{{item.activityName}}</md-table-cell>
                <md-table-cell>{{item.activityType}}</md-table-cell>
                <md-table-cell :key="index">{{moment(item.startTime).format('YYYY MM DD : h:mm:ss')}}</md-table-cell>
                <md-table-cell :key="index"><p v-if="item.endTime !== null">{{moment(item.endTime).format('YYYY MM DD : h:mm:ss')}}</p></md-table-cell>
                <md-table-cell>{{item.duration}}</md-table-cell>
                <md-table-cell>
                  <router-link v-if="item.calledProcessInstanceId !== null" class="link" :to="'/details/' + item.calledProcessInstanceId">{{item.calledProcessInstanceId}}</router-link>
                </md-table-cell>
                <md-table-cell v-if="item.taskId !== null">
                  <router-link v-if="item.endTime === null" class="link" :to="'/task/' + item.taskId">Tâche {{item.taskId}}</router-link>
                  <p v-if="item.endTime !== null">{{item.taskId}}</p>
                </md-table-cell>
                <md-table-cell v-else>
                </md-table-cell>
                <md-table-cell>
                  <router-link v-if="item.assignee !== null" class="link" :to="'/task/?assignee=' + item.assignee">Utilisateur</router-link>
                </md-table-cell>
               </md-table-row>
             </md-table-body>
          </md-table>
          </md-theme>
      </div>
    </div>
  </div>
</template>

<script>
  import VueResource from 'vue-resource'
  let moment = require('moment')

  export default {
    name: 'Running',
    components: {
      VueResource
    },
    data () {
      return {
        moment: moment,
        id: '',
        processInstance: '',
        activities: []
      }
    },
    mounted: function () {
      this.id = this.$route.params.id
      this.getProcessInstance()
      this.getProcessInstanceActivities()
    },
    watch: {
      $route: function () {
        this.id = this.$route.params.id
        this.getProcessInstance()
        this.getProcessInstanceActivities()
      }
    },
    methods: {
      getProcessInstance () {
        this.$http.get(process.env.PROCESS_URL + ':' + process.env.PROCESS_PORT + '/rest/processInstance/' + this.id, {
          headers: {
            'Accept': 'application/json'
          }
        })
        .then((response) => {
          console.log(response)
          this.processInstance = response.data
        })
        .catch(function (data, status, request) {
          console.log(data)
        })
      },
      getProcessInstanceActivities () {
        this.$http.get(process.env.PROCESS_URL + ':' + process.env.PROCESS_PORT + '/rest/processInstance/' + this.id + '/activity', {
          headers: {
            'Accept': 'application/json'
          }
        })
        .then((response) => {
          console.log(response)
          this.activities = response.data
        })
        .catch(function (data, status, request) {
          console.log(data)
        })
      }

    }
  }
</script>



