<template>
  <div>
    <div id="content">
      <h2>Variables du process <router-link class="link" :to="'/details/' + id">{{id}}</router-link></h2>
      <div id="listImages" v-if="variablesInstances.length > 0">
          <md-theme md-name="table" md-theme="blue">
          <md-table>
            <md-table-header>
              <md-table-row>
               <md-table-head md-sort-by="process">Id</md-table-head>
               <md-table-head>Type</md-table-head>
               <md-table-head>Nom</md-table-head>
               <md-table-head>Valeur</md-table-head>
             </md-table-row>
             </md-table-header>
             <md-table-body>
               <md-table-row v-for="(item, index) in variablesInstances" :key="index">
                <md-table-cell>{{item.id}}</md-table-cell>
                <md-table-cell>{{item.type}}</md-table-cell>
                <md-table-cell>{{item.name}}</md-table-cell>
                <md-table-cell>{{item.value}}</md-table-cell>
               </md-table-row>
             </md-table-body>
          </md-table>
          </md-theme>
      </div>
    </div>
  </div>
</template>

<script>
  import VueResource from 'vue-resource'
  let moment = require('moment')

  export default {
    name: 'Running',
    components: {
      VueResource
    },
    data () {
      return {
        id: '',
        moment: moment,
        variablesInstances: []
      }
    },
    mounted: function () {
      this.id = this.$route.params.id
      this.getVariablesInstances()
    },
    methods: {
      getVariablesInstances () {
        this.$http.get(process.env.PROCESS_URL + ':' + process.env.PROCESS_PORT + '/rest/processInstance/' + this.id + '/variable', {
          headers: {
            'Accept': 'application/json'
          }
        })
        .then((response) => {
          console.log(response)
          this.variablesInstances = response.data
        })
        .catch(function (data, status, request) {
          console.log(data)
        })
      }
    }
  }
</script>



