<template>
  <div>
    <div id="content" class="geotiff-viewer">
      <div id="form">
        <div id="search">
          <h2>PARAM&Egrave;TRES ET RECHERCHE</h2>
          <div class="form-group">
	          <h3 :class="extend1 ? 'extend' : ''" @click="extend1 = !extend1">PARAM&Egrave;TRES DE L'IMAGE</h3>
	          <div class="group-content">
		          <div class="input-group">
		            <h4>Opacité</h4>
		             <input type="range" min="0" max="1" step="0.1" v-model="opacity" @change="changeOpacity" :disabled="disabled"/>
		          </div>
		          <div class="input-group">
		            <h4>Echelle de couleurs</h4>
		             <select class="select-color-scale" v-model="colorScale" @change="changeColorScale" :disabled="disabled">
		              <option v-for="(item, index) in colorScales" :key="index">{{item}}</option>
		             </select>
		          </div>
		          <div class="input-group">
		            <h4>Minimum et maximum sur l'échelle de couleur</h4>
		            <formater-double-range :disabled="disabled" :colorScale="colorScale" :width="250" :min="rangeScale.min" :max="rangeScale.max" :defaultRange="defaultRange" v-model="displayed" @change="changeMinMax"></formater-double-range>
		          </div>
		          <div class="input-group">
		            <h4>Valeurs à afficher</h4>
	                <div>
	                    <input type="checkbox" v-model="clamp.low" @change="changeClamp" :disabled="disabled">
	                    <label>Afficher les valeurs inférieures à {{displayed ? displayed.min : defaultRange.min}}</label>
	                </div>
		            <div>
		                <input type="checkbox" v-model="clamp.high" @change="changeClamp" :disabled="disabled">
		                <label>Afficher les valeurs supérieures à {{displayed ? displayed.max : defaultRange.max}}</label>
		            </div>
		          </div>
	          </div>
         </div>
         <div class="form-group">
	         <h3 :class="extend2 ? 'extend' : ''" @click="extend2 = !extend2">RECHERCHE</h3>
	           <div class="group-content">
		         <div class="input-group">
		         <h4>Minimum et maximum</h4>
		         <input type="button" value="Recherche des optima" :disabled="(disabled || optima.min != null)" @click="searchOptima">
		         <ul>
		         <li><span class="label">Minimum</span>: {{(optima.min == null) ? '--' : optima.min | number}}</li>
		         <li><span class="label">Maximum</span>: {{(optima.max == null) ? '--' : optima.max | number}}</li>
		         </ul>
		         </div>
		         <div class="input-group">
		            <h4>Mode</h4>
		            <div>
		            <input type="radio" v-model="mode" value="point" :disabled="disabled"><label>Point</label>
		            <input type="radio" v-model="mode" value="profile" :disabled="disabled"><label>Profile</label>
		            </div>
		            <div class="instruction" v-if="mode == 'point'">Cliquez sur l'image afin d'obtenir la valeur en un point.</div>
		            <div class="instruction" v-else>Cliquer sur la carte, pour tracer un chemin.... @todo</div>
		         </div>
		    </div>
          </div>
      </div>
      </div>
      <div id="mapTiff">
        <div class="map"></div>
      </div>       
    </div>
  </div>


</template>

<script>
/* eslint no-unused-vars: "off" */
 // import Datepicker from 'vuejs-datepicker'
  import VueResource from 'vue-resource'
  import Vue from 'vue'
  import L from '../module/leaflet-geotiff-plotty.js'
  import FormaterDoubleRange from './elements/formater-double-range'
  var plotty = require('plotty')
  Vue.filter('number', function (value) {
    if (value.toFixed) {
      value = value.toFixed(2).toString().replace('.', ',').replace('-', '- ')
    }
    return value
  })
  /* let icon = new L.Icon({
    iconUrl: require('../assets/images/marker-icon.png'),
    iconRetinaUrl: require('../assets/images/marker-icon-2x.png'),
    shadowUrl: require('../assets/images/marker-shadow.png'),
    iconSize: [25, 41],
    iconAnchor: [12, 41],
    popupAnchor: [1, -34],
    tooltipAnchor: [16, -28],
    shadowSize: [41, 41]
  }) */

  Vue.use(VueResource)

  export default {
    name: 'GeotiffViewer',
    components: {
      VueResource,
      // Datepicker,
      FormaterDoubleRange
    },
    data () {
      return {
        extend1: true,
        extend2: true,
        disabled: true,
        map: null,
        renderer: null,
        geotiff: null,
        interactiveImage: null,
//         zoom: 2,
//         center: [47.413220, -1.219482],
//         url: '//{s}.tile.opentopomap.org/{z}/{x}/{y}.png',
//         attribution: '&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors',
        rangeScale: {
          min: -200,
          max: 200
        },
        defaultRange: {
          min: -50,
          max: 50
        },
        opacity: 0.7,
        displayed: null,
        colorScale: 'rdbu',
        clamp: {
          low: true,
          high: true
        },
        optima: {
          min: null,
          max: null
        },
        mode: 'point',
        // @todo récupérer les noms des fichiers
        tiffs: ['https://api.poleterresolide.fr/geotiff/geo_TOT_20170818.unw.tiff'],
        // @todo récupérer le tableau colorScales de Plotty plotty/colorscales.js
        colorScales: Object.keys(plotty.colorscales).sort()
      }
    },
    mounted: function () {
      this.initMap()
      this.loadGeotiff(this.tiffs[0])
    },
    methods: {
      /**
      * initialize the map
      */
      initMap: function () {
        var container = this.$el.querySelector('div.map')
        var _this = this
        this.map = L.map(container).setView([0, 0], 1)
        L.tileLayer('//server.arcgisonline.com/ArcGIS/rest/services/World_Topo_Map/MapServer/tile/{z}/{y}/{x}',
          {
            attribution: 'Tiles © <a href="https://services.arcgisonline.com/ArcGIS/rest/services/World_Topo_Map/MapServer">ArcGIS</a>',
            maxZoom: 18,
            minZoom: 2
          }).addTo(this.map)
        this.interactiveImage = L.imageOverlay(
          'https://api.poleterresolide.fr/images/transparent.png',
          [[0, 0], [0.02, 0.2]],
          {
            crossOrigin: true,
            zIndex: 2000,
            interactive: true,
            bubblingMouseEvents: false,
            alt: 'mon geotiff'
          }).on('click', function (event) {
            _this.searchOnGeotiffLayer(event)
          })
      },
      /**
      * Load the geotiff "filepath"  and add it to the map
      * With the colorScale in this.colorScale
      * displayMin
      * @param String filepath file url
      */
      loadGeotiff: function (filepath) {
        var options = {
          displayMin: this.defaultRange.min,
          displayMax: this.defaultRange.max,
          clampLow: this.clamp.low,
          clampHigh: this.clamp.high,
          colorScale: this.colorScale
        }
        var opacity = this.opacity
        this.renderer = L.LeafletGeotiff.plotty(options)
        this.geotiff = L.leafletGeotiff(filepath,
          {
            renderer: this.renderer,
            band: 0
          })
        var _this = this
        this.geotiff.once('load', function () {
          var bounds = this.getBounds()
          _this.map.fitBounds(bounds)
          _this.disabled = false
          this.setOpacity(opacity)
          _this.interactiveImage.addTo(_this.map)
          _this.setImagePosition(bounds)
        })
        this.geotiff.addTo(this.map)
        // this.renderer.setColorScale(this.colorScale)
      },
      setImagePosition: function (bounds) {
        this.interactiveImage.setBounds(bounds)
      },
      changeColorScale: function () {
        this.renderer.setColorScale(this.colorScale)
      },
      changeOpacity: function () {
        this.geotiff.setOpacity(this.opacity)
      },
      changeMinMax: function (displayed) {
        this.displayed = displayed
        this.renderer.setDisplayRange(displayed.min, displayed.max)
      },
      changeClamp: function () {
        this.renderer.setClamp(this.clamp.low, this.clamp.high)
      },
      reset: function () {
        if (this.geotiff) {
          this.geotiff.remove()
          this.geotiff = null
          this.interactiveImage.remove()
        }
        // @todo reset double-range aussi
        this.optima.min = null
        this.optima.max = null
        this.disabled = true
      },
      searchOnGeotiffLayer: function (event) {
        switch (this.mode) {
          case 'point':
            var value = this.geotiff.getValueAtLatLng(event.latlng.lat, event.latlng.lng)
            var popup = L.popup({closePopupOnClick: false})
              .setLatLng(event.latlng)
              .setContent('Valeur: ' + value.toFixed(2).toString().replace('.', ',').replace('-', '- '))
              .openOn(this.map)
            break
          case 'profile':
            break
        }
      },
      searchOptima: function () {
        this.optima = this.geotiff.getMinMaxValues()
      }
    }
  }
</script>

<style scoped>
/*  @import "../../node_modules/leaflet-draw/dist/leaflet.draw.css";
  @import "../../node_modules/leaflet/dist/leaflet.css";*/
  #content{
    width: 100%;
    height:100%;
  }
  .geotiff-viewer #mapTiff {
      margin-left:330px;
      margin-right:10px;
      position:relative;
      height: 80vh;
       box-sizing: border-box;
    
  }
  #mapTiff div.map {
    position: absolute;
    min-height: 600px;
    height:100%;
    width:100%;
  }
  #form {
    display: inline-block;
    position: relative;
    left: 0.5%;
    width: 20%;
    background-color: #f0f0f0;
    float: left;
    min-width:320px;
    width: 320px;
    padding:5px 10px 10px 10px;
  }
  .input-group select,
  .input-group input[type="range"]{
   width: 250px;
   margin-left: 20px;
  }
 .input-group{
   margin: 20px 0 20px 5px;
   clear: both;
  }
 .input-group h4{
     margin-bottom:10px;
 }
 .form-group{
    border-top: 1px solid grey;
    padding-bottom: 15px;
 }
 span.label{
    font-weight: 500;
 }
 #form h3{
 	cursor: pointer;
 }
 h3::before{
    content: "\23F5";
    font-size:1.2em;
    padding-right: 5px;
    margin-left: -5px;
 }
 h3.extend::before{
    content: "\23F7";
 }
 h3 + .group-content{
 	max-height:0px;
 	height:auto;
 	overflow:hidden;
 	transition: max-height .5s ease;
 }
 h3.extend + .group-content{
    max-height:600px;
    transition: max-height .5s ease;
 }
 div.instruction{
  padding: 5px 10px;
  line-height:1;
  color: grey;
 }
</style>




