<template>

  <div>
    <div id="content">
      <div id="form">
        <div id="search">
          <h3>Rechercher des images</h3>
          <span>Choisir des images qui se superposent parfaitement. <br/> Eviter les surfaces aquatiques et la végétation.</span>
          <br/>
          <br/>
          <div style="clear:both" />
          <span style="width:100px;float:left" >Début : </span>
          <datepicker class="datepick" v-model="start"/>
          <span style="float:left;clear:both" />
          <br/>
          <span style="width:100px;float:left;">Fin : </span>
          <datepicker class="datepick" v-model="end" />
          <span style="clear:both;float:left" />
          <br/>
          <select v-model="selected">
             <option disabled value="">SATELLITE</option>
             <option value="SENTINEL-1" selected="true">SENTINEL-1</option>
          </select>
          <br/>
          <select v-model="instrument">
             <option disabled value="">INSTRUMENT</option>
             <option value="SAR-C%20SAR" selected="true">SAR bande C</option>
          </select>

          <br/>
          <select v-model="traitement">
             <option disabled value="">NIVEAU DE TRAITEMENT</option>
             <option value="LEVEL1" selected="true">LEVEL1</option>
          </select>
          <br/>
          <select v-model="product">
            <option disabled value="">TYPE DE PRODUIT</option>
            <option value="SLC" selected="true">SLC</option>
          </select>
          <br/>
          <!-- Sandrine -->
          <select v-model="plateforme">
            <option disabled value="">TYPE DE PLATEFORME</option>
            <option value="S1A%20S1B" selected="true">S1A S1B</option>
            <option value="S1A" selected="false">S1A</option>
            <option value="S1B" selected="false">S1B</option>
          </select>
          <br/>
          <select v-model="modeCapteur">
            <option disabled value="">MODE DE CAPTEUR</option>
            <option value="IW" selected="true">IW</option>
            <option value="SM" selected="false">SM</option>
          </select>
          <br/>
          <!--
          <select v-model="typePolarisation">
            <option value="">POLARISATION</option>
            <option value="VV" selected="true">VV</option>
            <option value="VH" selected="false">VH</option>
            <option value="VV%20VH" selected="false">VV VH</option>
            <option disabled value="polarisationInterferogramme" selected="false">VV VH dual</option>
          </select>
          <br/>
          -->
          <select v-model="sensOrbite">
            <option value="">SENS DE L'ORBITE</option>
            <option value="ascending" selected="true">ASCENDANTE</option>
            <option value="descending" selected="false">DESCENDANTE</option>
          </select>
          <br/>
          <!-- Sandrine -->
          <br/>
          <button name="button" v-on:click="getImages">Rechercher les images</button>
          <br/><br/>
          <span v-if="((OrbiteRelativeSelected === '') && (this.totalResult > 0))">
            <button name="button" v-on:click="getMoreImages(false)">Voir plus d'images d'exploration...</button>
          </span>
          <br/><br/>
          <button name="button" :disabled="true" v-on:click="getImagesOPGC">Rechercher les images OPGC</button>
        </div>
      </div>
      <div id="mapArea">
        <!--<button name="button" v-on:click="switchMapStyle">Switch map style</button>-->
        <v-map style="height: 100%" :zoom="zoom" :center="center">
          <v-tilelayer :url="url" :attribution="attribution"></v-tilelayer>
          <v-marker v-for="item in markers" :key="item.id" :lat-lng="item.position" :visible="item.visible" :draggable="item.draggable"
                    v-on:l-move="markerMoved($event, item)" :icon="img">
            <v-popup :content="item.tooltip"></v-popup>
            <v-tooltip :content="item.tooltip"></v-tooltip>
          </v-marker>
          <v-marker v-if="areaMarker !== ''" :lat-lng="areaMarker" :visible="true" :icon="img">
            <v-tooltip :content="areaMarkerTooltip"></v-tooltip>
          </v-marker>
          <<v-polygon v-for="item in displayedImages" :key="item.id" :lat-lngs="item.geometry.coordinates" :visible="true" v-on:mouseover="processOnMousseOver(item)" :color="imagesColor">
            <!--<v-tooltip :content='TOOLTIP'></v-tooltip>-->
          </v-polygon>
          <v-circle v-for="item in circles" :key="item.id" :lat-lng="item.center" :radius="item.radius"></v-circle>
         <v-rectangle v-for="item in rectangles" :key="item.id" :bounds="item.bounds" :color="item.color"></v-rectangle>
        </v-map>
      </div>
      <div id="filtrageInterfero" v-if="this.totalResult > 0">
        <h3>Critères interférométriques des produits</h3>
        <!-- Sandrine -->
        <span>Fauchées disponibles: </span>
        <span v-for="(item,index) in this.swathsDispo" :key="index">
          <button name="button" v-on:click="seeSwathSelected(item.numero, item.sens, true)">N° <b>{{item.numero}}</b> <b>{{item.sens}}</b> <small>({{item.nbImages}} images)</small></button>
          <!--<button name="button" v-on:click="seeOrbiteRelative(item.numero, item.sens)" v-on:mouseover="changeColor(item.numero, item.sens, true)"
          v-on:mouseleave="changeColor(item.numero, item.sens, false)">{{'Fauchée N° ' + item.numero + ' ' + item.sens + ' (' + item.nbImages + ' images)'}}</button>
        -->
        </span>

        <button name="button" v-on:click="seeSwathSelected(0, 0, false)" :color="imagesColor">Toutes les Fauchées</button>
        <br/>
        <!--<select v-model="typePolarisation">
          <option disabled value="">POLARISATION</option>
          <option value="VV_VH_DUAL" selected="true">VV VH dual</option>
        </select> -->
        <br/>
        <button name="button" v-on:click="getMoreImages(true)">Voir plus d'images <b>{{this.OrbiteRelativeSelected}}</b>...</button>
        <button name="button" v-on:click="getAllImagesFiltrageInterfero" :disabled="true">Voir toutes les images <b>{{this.OrbiteRelativeSelected}}</b>...</button>
        <br/>
      </div>
      <div id="launchProcess">
          <h3>Lancer un process</h3>
          <!--Email : <input id="emailAdress" class="mail" v-on:change="checkLaunch" v-model="email" placeholder="Enter your email">-->
        <span v-if="this.$session.exists()">
          <button name="button" v-on:click="launchProcess" :disabled="processLaunchDisabled">Lancer le process</button>
        </span>
        <span v-else>
          Veuillez d'abord vous identifier pour lancer un process<br/><button :disabled="true">Lancer le process</button>
        </span>
      </div>
      <div class="listImages" v-if="this.totalResult > 0">
        <span><b>{{ this.totalResult }}</b> images trouvées, <b>{{ this.filteredImages.length }}</b> images filtrées pour propriétés, <b>{{ this.selectedImages.length }}</b> filtrées pour sélection </span>
        <<div id="results" v-for="(item,index) in selectedImages">
          {{item.productIdentifier}}
          <div class="large-2 columns">
            <div class="thumbnail th text-center">
               <img :src="item.properties.quicklook" v-on:click="openWindow(item.properties.collection, item.properties.id)">
            </div>
          </div>
          <div class="large-3 columns">
            <span class="light">
               Product : <span class="blue" v-on:click="openWindow(item.properties.collection, item.properties.id)"> <u>{{item.id}}</u> </span>
            </span>
            <br/>
            <span class="light">
               Collection : {{item.properties.collection}}
            </span>
            <br/>
            <span class="light">
              Date : {{item.properties.startDate}}
            </span>
            <br/>
            <span class="light">
              Localisation : {{item.localisation}}
            </span>
            <br/>
          </div>
          <div class="large-3 columns">
            <span class="light">
              Plateforme : {{item.properties.platform}}
            </span>
            <br/>
            <span class="light">
              Instrument : {{item.properties.instrument}}
            </span>
            <br/>
            <span class="light">
              Type de produit : {{item.properties.productType}}
            </span>
            <br/>
            <span class="light">
              Produit NRT : {{item.properties.isNrt}}
            </span>
            <br/>
            <span class="light">
              Numero de fauchée : {{item.properties.swathNumber}}
            </span>
            <br/>
          </div>
          <div class="large-3 columns">
            <span class="light">
              Numéro d'orbite : {{item.properties.orbitNumber}}
            </span>
            <br/>
            <span class="light">
              Niveau de Traitement : {{item.properties.processingLevel}}
            </span>
            <br/>
            <span class="light">
              Mode du Capteur : {{item.properties.sensorMode}}
            </span>
            <br/>
            <span class="light">
              Polarisation : {{item.properties.polarisation}}
            </span>
            <br/>
            <span class="light">
              Orbit Direction : {{item.properties.orbitDirection}}
            </span>
            <br/>
          </div>
          <div class="large-4 columns">
               Afficher l'emprise : <input type="checkbox" v-on:click="restrictDisplay(item)" checked="checked" /><br/>
               <!-- SM Selectionner image : <input type="checkbox" v-model="checks[index]" v-on:click="addToProcessParams(item,index)" /><br/> -->
               <span v-if="(OrbiteRelativeSelected !== '') && (geometrieSelected)">
                 Selectionner image : <input type="checkbox" v-model="checks[index]" v-on:click="addToSelectionImage(item,index)" /><br/>
                 Swaths : {{item.properties.swath}} <input type="checkbox" checked="checked" :disabled="true"/>
               </span>
               <br/>
               <span v-if="(OrbiteRelativeSelected !== '') && (!geometrieSelected)">
                 <button name="button" v-on:click="voirImagesDeLaGeometrie(item)">Voir les images de cette géométrie...</button>
               </span>
          </div>
        </div>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <div id="moreImages">
          <span v-if="OrbiteRelativeSelected === ''">
            <button name="button" v-on:click="getMoreImages(false)">Voir plus d'images d'exploration...</button>
          </span>
        </div>
      </div>
      <div class="listImages" v-else>
        Pas d'images avec les bonnes données
      </div>
    </div>
  </div>


</template>

<script>

  import Datepicker from 'vuejs-datepicker'
  import Vue2Leaflet from 'vue2-leaflet'
  import L from 'leaflet'
  import VueResource from 'vue-resource'
  import Vue from 'vue'
  import geojsonData from '../assets/geojson/GeoJsonZonesInteretFormaterEtalab.json'
  import Moment from 'moment' // Sandrine
  require('leaflet-draw')
  let jsonPath = require('jsonpath')
  // let GeojsonEquality = require('geojson-equality')

  let data = JSON.parse(JSON.stringify((geojsonData)))
  let markerList = []
  let circleList = []
  let icon = new L.Icon({
    iconUrl: require('../assets/images/marker-icon.png'),
    iconRetinaUrl: require('../assets/images/marker-icon-2x.png'),
    shadowUrl: require('../assets/images/marker-shadow.png'),
    iconSize: [25, 41],
    iconAnchor: [12, 41],
    popupAnchor: [1, -34],
    tooltipAnchor: [16, -28],
    shadowSize: [41, 41]
  })

  let processParamsVar = new Map()

  for (let i = 0; i < data.features.length; i++) {
    markerList.push({'id': i, 'position': {'lat': data.features[i].geometry.coordinates[1], 'lng': data.features[i].geometry.coordinates[0]}, 'tooltip': data.features[i].properties.Label, 'draggable': false, 'visible': true})
    circleList.push({'id': i, 'center': L.latLng(data.features[i].geometry.coordinates[1], data.features[i].geometry.coordinates[0]), 'radius': 50000})
  }

  Vue.use(VueResource)

  export default {
    name: 'Start',
    components: {
      VueResource,
      Datepicker,
      'v-map': Vue2Leaflet.Map,
      'v-tilelayer': Vue2Leaflet.TileLayer,
      'v-geojson-layer': Vue2Leaflet.GeoJSON,
      'v-marker': Vue2Leaflet.Marker,
      'v-circle': Vue2Leaflet.LCircle,
      'v-tooltip': Vue2Leaflet.Tooltip,
      'v-popup': Vue2Leaflet.Popup,
      'v-rectangle': Vue2Leaflet.Rectangle,
      'v-polygon': Vue2Leaflet.Polygon
    },
    data () {
      return {
        zoom: 2,
        center: [47.413220, -1.219482],
        url: 'http://{s}.tile.opentopomap.org/{z}/{x}/{y}.png',
        attribution: '&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors',
        markers: markerList,
        circles: circleList,
        option: {
          format: 'YYYY-MM-DD'
        },
        rectangles: [],
        rectangle: '',
        images: [],
        displayedImages: [],
        filteredImages: [],
        selectedImages: [],
        processImages: new Map(),
        processLaunchDisabled: true,
        start: '',
        startDate: '',
        end: '',
        endDate: '',
        email: '',
        selected: 'SENTINEL-1',
        instrument: 'SAR-C%20SAR',
        traitement: 'LEVEL1',
        orbite: 'Ascendant',
        polarisation: 'Ascendant',
        product: 'SLC',
        plateforme: 'S1A%20S1B',
        modeCapteur: 'IW',
        typePolarisation: 'VV_VH_DUAL',
        sensOrbite: '',
        orbiteRelative: '',
        startIndex: 1,
        latMarker: '',
        lngMarker: '',
        areaSize: 50,
        areaMarker: '',
        areaMarkerTooltip: 'Selected area',
        path: '/assets/images/',
        img: icon,
        mapObject: [],
        imagesColor: 'green',
        swath: '',
        swathsDispo: [], // Liste des Swaths (orbite relative) disponibles contient objet avec attributs (numero, sens, nbImages, seeOrbiteRelative)
        OrbiteRelativeSelected: '', // indique string "'Fauchée ' + <orbiteRelative> + ' ' + <orbiteDirection>" de la selection des fauchées dispon
        geometrieSelected: false,
        processParams: processParamsVar,
        selection: new Map(),
        checks: [],
        totalResult: 0,
        currentImageSelection: false
      }
    },
    mounted: function () {
      this.mapObject = this.$children[2].mapObject
      var drawnItems = new L.FeatureGroup()
      this.mapObject.addLayer(drawnItems)
      var drawControl = new L.Control.Draw({
        draw: {
          rectangle: {
            shapeOptions: {
              color: '#FFFF00'
            }
          },
          marker: false,
          circle: false,
          polygon: true,
          polyline: false
        },
        edit: {
          featureGroup: drawnItems
        }
      })
      this.mapObject.addControl(drawControl)

      let _this = this // SM

      this.mapObject.on(L.Draw.Event.CREATED, function (e) {
        let layer = e.layer
        let bounds = e.layer.getBounds()
        let newArea = {'bounds': [[bounds.getNorthEast().lat, bounds.getNorthEast().lng], [bounds.getSouthWest().lat, bounds.getSouthWest().lng]], 'color': 'red'}
        this.rectangle = newArea

        e.layer.bindPopup('Zone de sélection')

        drawnItems.clearLayers()
        drawnItems.addLayer(layer)

        _this.nettoyerCarte()
        _this.getImages() // Relance automatique des images avec les critères généraux de recherche
      })

      this.mapObject.on(L.Draw.Event.EDITED, function (e) {
        console.log(e)
        let bounds
        e.layers.eachLayer(function (layer) {
          console.log(layer.getBounds())
          bounds = layer.getBounds()
        })
        let newArea = {'bounds': [[bounds.getNorthEast().lat, bounds.getNorthEast().lng], [bounds.getSouthWest().lat, bounds.getSouthWest().lng]], 'color': 'red'}
        this.rectangle = newArea

        _this.nettoyerCarte()
        _this.getImages() // Relance automatique des images avec les critères généraux de recherche
      })

      this.mapObject.on(L.Draw.Event.DELETED, function (e) {
        console.log(e)
        let bounds
        e.layers.eachLayer(function (layer) {
          console.log(layer.getBounds())
          bounds = layer.getBounds()
        })
        let newArea = {'bounds': [[bounds.getNorthEast().lat, bounds.getNorthEast().lng], [bounds.getSouthWest().lat, bounds.getSouthWest().lng]], 'color': 'red'}
        this.rectangle = newArea

        _this.nettoyerCarte()
      })
    },

    methods: {
      /**************************************************************************************/
      // Nettoyage de la carte après evenement Draw
      /**************************************************************************************/
      nettoyerCarte () {
        // Netoyage de la carte et des listes sur modification du plolygone de sélection
        this.OrbiteRelativeSelected = ''
        this.geometrieSelected = false
        this.totalResult = 0
        this.images.splice(0, this.images.length)
        this.displayedImages.splice(0, this.displayedImages.length)
        this.filteredImages.splice(0, this.filteredImages.length)
        this.selectedImages.splice(0, this.selectedImages.length)
      },

      // SM Ne marche pas!
      processOnMousseOver (emprise) {
        console.log('processOnMousseOver')
      },

      // Test - NON FONCTIONNEL
      getImagesOPGC () {
        let requete = 'https://wwwobs.univ-bpclermont.fr/casoar/image.php?Type=Sentinel&Folder=OI2_REU&Data=fournaise_SENTINEL-1A_SLC_S6_AR_10016&Size=s'
        let authoSHA1 = '8D:5B:BC:0A:74:BD:EF:D4:EB:5C:4C:D1:40:F7:66:3E:60:AC:9A:E6'
        this.$http.jsonp(requete, {
          headers: {
            'Accept': 'image/jpeg',
            'Content-Type': 'image/jpeg',
            'Authorization': 'Basic ' + authoSHA1
          }
        })
        .then((response) => {
          console.log(response)
        })
      },

      getImages () {
        this.startIndex = 1
        this.OrbiteRelativeSelected = ''
        this.geometrieSelected = false
        let startDate = ''

        if (this.start !== '') {
          let monthStart = (this.start.getMonth() + 1)
          let dayStart = this.start.getDate()

          if (this.start.getMonth() + 1 < 10) {
            monthStart = '' + 0 + (this.start.getMonth() + 1)
          }
          if (this.start.getDate() < 10) {
            dayStart = '' + 0 + this.start.getDate()
          }
          startDate = '' + this.start.getFullYear() + '-' + monthStart + '-' + dayStart + 'T00:00:00'
          this.startDate = startDate
        }

        let endDate = ''

        if (this.end !== '') {
          let monthEnd = (this.end.getMonth() + 1)
          let dayEnd = this.end.getDate()

          if (this.end.getMonth() + 1 < 10) {
            monthEnd = '' + 0 + (this.end.getMonth() + 1)
          }
          if (this.end.getDate() < 10) {
            dayEnd = '' + 0 + this.end.getDate()
          }
          endDate = '' + this.end.getFullYear() + '-' + monthEnd + '-' + dayEnd + 'T00:00:00'
          this.endDate = endDate
        }
        let request = 'https://peps.cnes.fr/resto/api/collections/S1/search.json?maxRecords=50&index=' + this.startIndex + '&productType=' + this.product + '&processingLevel=' + this.traitement + '&sensorMode=' + this.modeCapteur + '&instrument=' + this.instrument
        // Ajouter la plateforme soit S1A soit S1B si rien c'est les 2
        if (this.plateforme === 'S1A' | this.plateforme === 'S1B') {
          request += '&platform=' + this.plateforme
        }
        if (this.sensOrbite !== '') {
          request += '&orbitDirection=' + this.sensOrbite
        }

        console.log(this.mapObject.rectangle)
        if (this.mapObject.rectangle !== undefined) {
          request += '&box=' + this.mapObject.rectangle.bounds[0][1] + ',' + this.mapObject.rectangle.bounds[0][0] + ',' + this.mapObject.rectangle.bounds[1][1] + ',' + this.mapObject.rectangle.bounds[1][0]
        }
        if (startDate !== '') {
          request += '&startDate=' + startDate
        }
        if (this.endDate !== '') {
          request += '&completionDate=' + endDate
        }
        if (this.swath !== '') {
          request += '&swath=' + this.swath
        }
        console.log(request)

        // Reinitialisation du tableau des orbites relatives disponibles
        let totalASupprimer = this.swathsDispo.length
        for (let i = 0; i <= totalASupprimer; i++) {
          this.swathsDispo.pop()
        }

        this.$http.jsonp(request, {
          headers: {
            'Accept': 'application/json'
          }
        })
        .then((response) => {
          this.totalResult = response.data.properties.totalResults
          console.log('totalResult' + this.totalResult)
          let _this = this  // Permet de recuperer le lien d'ici dans la fonction orbitAbsolueCorrection

          let filtered = response.data.features.filter(function (e) {
            // Correction des metadonnées de l'orbite absolue avant calcul de l'orbite relative
            let orbiteAbsolueCoherence = false
            // let element = e
            orbiteAbsolueCoherence = _this.orbitAbsolueCorrection(e)
            return orbiteAbsolueCoherence
          })

          filtered.forEach(function (element, elementIndex, elementArray) {
            let pays = jsonPath.query(element.properties, '$..keywords[?(@.type=="country")].name')
            element.localisation = pays

            // Sandrine Recalcul de l'orbite relative en fonction de l'orbite absolue eventuellement corrigée
            _this.orbiteRelativeCalcul(element, _this.swathsDispo)

            element.geometry.coordinates[0].forEach(function (e, index, array) {
              let tempCoord = [e[1], e[0]]
              array[index] = tempCoord
            })
          }, jsonPath)
          this.swathsDispo.sort(function (a, b) {
            return a.numero - b.numero
          })

          filtered.sort(function (a, b) {
            return a.properties.swathNumber - b.properties.swathNumber
          })
          this.images = filtered
          this.displayedImages = filtered
          this.filteredImages = filtered
          this.checks = new Array(filtered.length).fill(false)
          this.selectedImages = filtered

          /*
          this.mapObject.rectangle.bindPopup(this.totalResult + ' Produits ' + '& ' + this.filteredImages + ' Produits filtrés')
          this.mapObject.rectangle.openPopup()
          this.mapObject.rectangle.bringToFront()
          */
          // SM Pour Correction temporaire du getMore avec doublon...
          this.seeSwathSelected(0, 0, false)

          console.log('Reponse PEPS ==> ')
          console.log(response)
        })
        .catch(function (data, status, request) {
          console.log('erreur requete PEPS')
        })
      },

      getMoreImages (filtrageCritereInterfero) {
        this.geometrieSelected = false
        this.startIndex = this.startIndex + 50
        let request = 'https://peps.cnes.fr/resto/api/collections/S1/search.json?maxRecords=50&index=' + this.startIndex + '&productType=' + this.product + '&processingLevel=' + this.traitement + '&sensorMode=' + this.modeCapteur + '&instrument=' + this.instrument

        if (this.plateforme === 'S1A' | this.plateforme === 'S1B') {
          request += '&platform=' + this.plateforme
        }
        if (this.sensOrbite !== '') {
          request += '&orbitDirection=' + this.sensOrbite
        }
        console.log(this.mapObject.rectangle)
        if (this.mapObject.rectangle !== undefined) {
          request += '&box=' + this.mapObject.rectangle.bounds[0][1] + ',' + this.mapObject.rectangle.bounds[0][0] + ',' + this.mapObject.rectangle.bounds[1][1] + ',' + this.mapObject.rectangle.bounds[1][0]
        }
        if (this.startDate !== '') {
          request += '&startDate=' + this.startDate
        }
        if (this.endDate !== '') {
          request += '&completionDate=' + this.endDate
        }
        if (this.swath !== '') {
          request += '&swath=' + this.swath
        }
        console.log(request)

        this.$http.jsonp(request, {
          headers: {
            'Accept': 'application/json'
          }
        })
          .then((response) => {
            this.totalResult = response.data.properties.totalResults
            let _this = this
            let filtered = response.data.features.filter(function (e) {
              console.log(response)
              // Correction des metadonnées de l'orbite absolue avant calcul de l'orbite relative
              let orbiteAbsolueCoherence = false
              orbiteAbsolueCoherence = _this.orbitAbsolueCorrection(e)
              return orbiteAbsolueCoherence
            })
            filtered.forEach(function (element, elementIndex, elementArray) {
              let pays = jsonPath.query(element.properties, '$..keywords[?(@.type=="country")].name')
              element.localisation = pays

              // Recalcul de l'orbite relative en fonction de l'orbite absolue eventuellement corrigée
              _this.orbiteRelativeCalcul(element, _this.swathsDispo)

              element.geometry.coordinates[0].forEach(function (e, index, array) {
                let tempCoord = [e[1], e[0]]
                array[index] = tempCoord
              })
            }, jsonPath)

            // Trier les orbites relatives disponibles
            this.swathsDispo.sort(function (a, b) {
              return a.numero - b.numero
            })
            // Pour le getMore le classement doit être fait après le push avec images précedentes
            /* tri effectue apres concatenation
            filtered.sort(function (a, b) {
              return a.properties.swathNumber - b.properties.swathNumber
            })
            */

            if (this.processImages.size === 0) {
              this.checks.fill(false, this.filteredImages.length, this.filteredImages.length + filtered.length)
            } else {
              console.log('TODO')
            }
            this.images.push.apply(this.images, filtered)
            this.displayedImages.push.apply(this.displayedImages, filtered)

            // this.selectedImages.push.apply(this.selectedImages, filtered)
            // CETTE LIGNE PROVOQUE UNE ERREUR !!! this.filteredImages.push.apply(this.filteredImges, filtered)
            // ERREUR + image que 50 !! this.selectedImages.push.apply(this.selectedImages, filtered)
            // this.filteredImages.push.apply(this.filteredImges, filtered)   // Sandrine bug ligne en double
            // retrier avec l'ajout fait
            this.displayedImages.sort(function (a, b) {
              return a.properties.swathNumber - b.properties.swathNumber
            })
            // Sandrine Fin
            console.log('Reponse PEPS ==> ')
            console.log(response)

            if (filtrageCritereInterfero) { // On utilise le bouton "voir plus d'image de la Fauchée"
              _this.swathsDispo.forEach(function (e, index, array) {
                if (e.seeOrbiteRelative) {
                  let features = _this.images.filter(function (el) {
                    if ((el.properties.swathNumber === e.numero) && (el.properties.orbitDirection.slice(0, 1) === e.sens)) {
                      return true
                    } else {
                      return false
                    }
                  })
                  _this.displayedImages = features
                  _this.filteredImages = features
                  _this.selectedImages = features
                }
              })
            } else {
              _this.OrbiteRelativeSelected = ''
            }
          })
          .catch(function (data, status, request) {
            console.log('erreur requete PEPS' + data + status + request)
          })
      },

      /**************************************************************************************/
      // Action Affichage de toutes les images du Filtrage avec critère interferometrique
      /**************************************************************************************/
      getAllImagesFiltrageInterfero () {
        if (this.OrbiteRelativeSelected === '') {
          alert('Sélectionner une fauchée disponible')
        }
        // .... A finir
      },

      /**************************************************************************************/
      // Division d'une chaine selon un separateur pour donner un tableau de chaque elt
      /**************************************************************************************/
      splitString (stringToSplit, separator) {
        console.log('La chaine d\'origine est : ' + stringToSplit)
        // let chaine = 'dfkdflksqdnfjkldsfgjkldsbgfdsbgjdsbgjbdsjgbdjsbjd'
        console.log('La chaine d\'origine est : ' + stringToSplit)
        let arrayOfStrings = stringToSplit.toString().split(separator)

        console.log('Le délimiteur est : ' + separator)
        console.log('Le tableau comporte ' + arrayOfStrings.length + ' elements : ')

        let sortie = ''
        for (let i = 0; i < arrayOfStrings.length; i++) {
          sortie = sortie + arrayOfStrings[i] + ' / '
        }
        console.log('contenu tableau ' + sortie)
        return arrayOfStrings
      },

      /**************************************************************************************/
      // Comparer les coordonnées des tableaux selon l'arrondi et renvoyer un boolean
      /**************************************************************************************/
      coordonneesIdentiques (tableauCoordElt, tableauCoordItem) {
        let resultat = true
        let valeurArrondi = 0.15
        for (let i = 0; i < tableauCoordElt.length; i++) {
          if (Math.abs(tableauCoordElt[i] - tableauCoordItem[i]) <= valeurArrondi) {
            resultat = resultat && true
          } else {
            resultat = resultat && false
          }
        }
        return resultat
      },

      /**************************************************************************************/
      // Retenir les polarisations compatibles
      /**************************************************************************************/
      polarisationsCompatibles (ItemPolarisation, eltPolarisation) {
        let resultat = false
        if (ItemPolarisation === 'VV') {
          resultat = ((eltPolarisation === 'VV') || (eltPolarisation === 'VV VH'))
        } else if (ItemPolarisation === 'HH') {
          resultat = ((eltPolarisation === 'HH') || (eltPolarisation === 'HH HV'))
        } else if (ItemPolarisation === 'HV') {
          resultat = ((eltPolarisation === 'HV') || (eltPolarisation === 'HH HV'))
        } else if (ItemPolarisation === 'VH') {
          resultat = ((eltPolarisation === 'VH') || (eltPolarisation === 'VV VH'))
        } else if (ItemPolarisation === 'HH HV') {
          resultat = ((eltPolarisation === 'HH') || (eltPolarisation === 'HH HV') || (eltPolarisation === 'HV'))
        } else if (ItemPolarisation === 'VV VH') {
          resultat = ((eltPolarisation === 'VV') || (eltPolarisation === 'VH') || (eltPolarisation === 'VV VH'))
        } else {
          console.log('polarisation inconnue: ' + ItemPolarisation)
        }
        return resultat
      },

      /**************************************************************************************/
      // Affichage des images d'une meme géometrie de la liste images du critère interferometrique
      /**************************************************************************************/
      voirImagesDeLaGeometrie (item) {
        // ATT verifier et ne faire que si on a déja cliqué sur des criteres interferoùetriques....
        // filtrage des imagesdisplayed pour coordonnées exactement identiques, polarisations compatibles
        // + meme sens orbite et meme orbite relative
        this.geometrieSelected = true
        let _this = this
        let i = 0
        let separateur = ','
        console.log('item geometrie ' + item.geometry.coordinates[0])
        let tabCoordItem = this.splitString(item.geometry.coordinates[0], separateur)
        let features = this.displayedImages.filter(function (el) {
          if ((el.properties.swathNumber === item.properties.swathNumber) &&
          (el.properties.orbitDirection === item.properties.orbitDirection)) {
            console.log('element geometrie ' + el.geometry.coordinates[0])
            let tabCoordElt = _this.splitString(el.geometry.coordinates[0], separateur)
            if ((_this.coordonneesIdentiques(tabCoordElt, tabCoordItem)) && (_this.polarisationsCompatibles(item.properties.polarisation, el.properties.polarisation))) {
              i += 1
              return true
            } else {
              return false
            }
          } else {
            return false
          }
        })
        console.log('NB images identiques ' + i)
        this.displayedImages = features
        this.selectedImages = features
      },

      /**************************************************************************************/
      /* Highlight de l'emprise carte correspondant à l'orbite relative sélectionnée sur mousseover
      /**************************************************************************************/
      seeOrbiteRelative (swathNumber, swathSens) {
        // Action selectio orbite relative
        let features = this.images.filter(function (el) {
          console.log('seeOrbiteRelative =====> ' + ' ' + el.properties.swathNumber + ' = ' + swathNumber +
          el.properties.orbitDirection.slice(0, 1) + ' = ' + swathSens)
          if ((el.properties.swathNumber === swathNumber) && (el.properties.orbitDirection.slice(0, 1) === swathSens)) {
            return true
          }
          return false
        })
        this.displayedImages = features
        this.filteredImages = features
        this.selectedImages = features
      },

      /**************************************************************************************/
      /* Highlight de l'emprise carte correspondant à l'orbite relative sélectionnée sur clic
      /**************************************************************************************/
      seeSwathSelected (swathNumber, swathSens, orbiteRelativeSelection) {
        // On met à jour swathsDispo pour indiquer que cette fauchée est selectionnée
        let _this = this
        this.geometrieSelected = false
        this.swathsDispo.forEach(function (e, index, array) {
          if (orbiteRelativeSelection) {
            if ((e.numero === swathNumber) && (e.sens === swathSens)) {
              console.log('swathNumber = ' + swathNumber)
              _this.OrbiteRelativeSelected = 'Fauchée ' + swathNumber + ' ' + swathSens
              console.log('OrbiteRelativeSelected = ' + _this.OrbiteRelativeSelected)
              e.seeOrbiteRelative = true
            } else {
              e.seeOrbiteRelative = false
            }
          } else {
            _this.OrbiteRelativeSelected = ''
            console.log('OrbiteRelativeSelected else else= ' + _this.OrbiteRelativeSelected)
            e.seeOrbiteRelative = false
          }
        })
        // if (this.OrbiteRelativeSelected !== '') { // Aucune selection de fauchée, on autorise le mouseover des boutons
        console.log('ON EST ICI')
        let features = this.images.filter(function (el) {
          if (orbiteRelativeSelection) {
            console.log('seeSwathSelected =====> orbiteRelativeSelection:' + orbiteRelativeSelection + ' ' + el.properties.swathNumber + ' = ' + swathNumber +
            el.properties.orbitDirection.slice(0, 1) + ' = ' + swathSens)
            if ((el.properties.swathNumber === swathNumber) && (el.properties.orbitDirection.slice(0, 1) === swathSens)) {
              return true
            }
            return false
          } else { // soit la souris quitte le bouton orbiteRelative soit on clique sur toutes les orbites
            return true
          }
        })
        this.displayedImages = features
        this.filteredImages = features
        this.selectedImages = features
      },
/* Changement
      addToProcessParams: function (image, index) {
        console.log('in add')
        let polyGonEq = new GeojsonEquality({precision: 1})
        if (this.checks[index]) {
          console.log('check')
          let tempImages = this.selectedImages.filter(function (element, index, table) {
            this.selection.set(element.id, this.checks[index])
            if (image.properties.orbitDirection !== element.properties.orbitDirection) {
              return false
            } else if (image.properties.polarisation !== element.properties.polarisation) {
              return false
            } else if (image.properties.swathNumber !== element.properties.swathNumber) {
              return false
            } else if (polyGonEq.compare(image.geometry, element.geometry) === false) {
              return false
            }
            return true
          }, this)
          this.restrictChecks(tempImages)
          this.processImages.set(image.id, image)
          this.displayedImages = tempImages
          this.selectedImages = tempImages
        } else {
          console.log('unchecked')
          this.processImages.delete(image.id)
          this.selection.set(image.id, false)
          if (this.processImages.size === 0) {
            this.checks = []
            this.checks = new Array(this.filteredImages.length).fill(false)
            this.displayedImages = this.filteredImages
            this.selectedImages = this.filteredImages
          } else {
            this.restrictChecks(this.selectedImages)
          }
        }
        this.checkLaunch()
      },
*/

      /**************************************************************************************/
      /* Ajout d'une image pour le lancement du process de calcul
      /**************************************************************************************/
      addToSelectionImage: function (image, index) {
        console.log('addToSelectionImage')
        if (!this.checks[index]) {
          console.log('check')
          this.processImages.set(image.id, image)
        } else {
          console.log('unchecked')
          this.processImages.delete(image.id)
        }
        this.checkLaunch()
      },

      checkLaunch: function () {
        if (this.processImages.size === 2 && this.$session.exists()) {
          this.processLaunchDisabled = false
        } else {
          this.processLaunchDisabled = true
        }
      },

      restrictDisplay: function (image) {
        let feature = this.displayedImages.filter(function (el) {
          console.log(el.id + ' - ' + image.id)
          if (el.id === image.id) {
            return true
          }
          return false
        })
        console.log(feature)
        console.log(feature[0])
        if (feature === undefined || feature.length === 0) {
          let featureToAdd = this.filteredImages.filter((el) =>
            el.id === image.id
          )
          this.displayedImages.push(featureToAdd[0])
          console.log('add' + feature)
        } else {
          let features = this.displayedImages.filter((el) =>
            el.id !== image.id
          )
          this.displayedImages = features
        }
      },

      launchProcess: function () {
        let body = '{"email" : "' + this.$session.get('email') + '", "images": ['
        let first = true
        this.processImages.forEach(function (element, key, elementArray) {
          if (first) {
            first = false
          } else {
            body += ' , '
          }
          body += '"' + element.id + '"'
        })
        body += ']}'
        console.log(body)
        let request = process.env.PROCESS_URL + ':' + process.env.PROCESS_PORT + '/rest/process/interferogramme-main/processInstances'
        this.$http.post(request, body, {
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'processDefinitionKey': 'interferogramme-main'
          }
        })
        .then((response) => {
          console.log(response)
          console.log('/details/' + response.body)
          this.$router.push({name: 'Details', params: {id: response.body}})
        })
        .catch(function (data, status, request) {
          console.log(data)
        })
      },

      openWindow: function (collection, id) {
        window.open('https://peps.cnes.fr/rocket/#/collections/' + collection + '/' + id)
      },

      getLocalisation (element) {
        console.log('ask for localisation')
        return this.jsonPath.query(element.properties, '$..keywords[?(@.type=="country")].name')
      },

      restrictChecks (array) {
        this.checks = []
        let newIndex = 0
        array.forEach(function (element, index, table) {
          console.log(this.selection.get(element.id))
          if (this.selection.get(element.id) === undefined || this.selection.get(element.id) === false) {
            this.checks[newIndex] = false
          } else {
            this.checks[newIndex] = true
          }
          newIndex++
        }, this)
      },

      /***************************************************************************************************************/
      /* Correction, si necessaire, des metadonnées PEPS sur l'orbiteNumber.
      /***************************************************************************************************************/
      orbitAbsolueCorrection (e) {
        console.log('correction orbite absolue')
        let launchDate = 0
        if (e.properties.platform === 'S1A') {
          launchDate = Moment('2014-04-03') / 1000 // conversion millisecondees en secondes
        } else if (e.properties.platform === 'S1B') {
          launchDate = Moment('2016-04-22') / 1000 // conversion millisecondees en secondes
        }
        // Duration of one full orbit, in hours
        let orbitDuration = 1.64575430405602567843
        // Obbite absolue a tester pour voir sa cohérence
        let orbitNumberTest = e.properties.orbitNumber
        let orbitNumberTestEntier = orbitNumberTest.valueOf()  // convertit chaine en entier
        console.log('orbitNumberTest origine en entier ==> ' + orbitNumberTest)
        // Read start time
        let dataStartDate = Moment(e.properties.startDate.slice(0, e.properties.startDate.length - 6)) / 1000 // conversion millisecondees en secondes
        let absOrbitNumberTheoretical = ((dataStartDate - launchDate) / 3600) / orbitDuration  // rem: diff en millisecondees
        console.log('absOrbitNumberTheoretical ==> ' + absOrbitNumberTheoretical)

        // Au plus on effectue 3 tentatives de correction de l'orbitNumber
        for (let i = 1; i <= 4; i++) {
          if ((Math.abs(orbitNumberTestEntier - absOrbitNumberTheoretical) > 1000) && (i < 4)) {
            console.log('IL FAUT CORRIGER!')
            // try deleting last character (bug with PEPS for data earlier than 2017... hopefully will be solved soon)
            orbitNumberTest = (e.properties.orbitNumber.toString().slice(0, e.properties.orbitNumber.toString().length - i))
            orbitNumberTestEntier = orbitNumberTest.valueOf()
            console.log('reduction ==> ' + orbitNumberTestEntier)
          } else if ((Math.abs(orbitNumberTestEntier - absOrbitNumberTheoretical) > 1000) && (i === 4)) {
            // Malgré 3 corrections l'orbitNumber est encore faux. Quoi faire??
            console.log('!! orbitNumberTest eronnée !! ' + orbitNumberTest)
            return false
          } else {
            // Des corrections ont été effectuées, ou pas
            e.properties.orbitNumber = orbitNumberTestEntier
            let nbCorrection = i - 1
            console.log('orbitNumber corrigée ' + nbCorrection + ' fois ==> ' + e.properties.orbitNumber)
            return true
          }
        }
      }, // fin orbitAbsolueCorrection()

      /***************************************************************************************************************/
      /* Calcul de l'orbite relative en fonction de l'orbite absolue corrigée
      /***************************************************************************************************************/
      orbiteRelativeCalcul (element, swathsDispoTab) {
        let relativeOrbitNumber = 0
        if (element.properties.platform === 'S1A') {
          relativeOrbitNumber = 1 + ((element.properties.orbitNumber - 73) % 175)
          element.properties.swathNumber = relativeOrbitNumber
          console.log('relativeOrbitNumber ===========================> ' + relativeOrbitNumber)
        } else if (element.properties.platform === 'S1B') {
          relativeOrbitNumber = 1 + ((element.properties.orbitNumber - 27) % 175)
          element.properties.swathNumber = relativeOrbitNumber
          console.log('relativeOrbitNumber ===========================> ' + relativeOrbitNumber)
        }
        // Completer swathsDispo par une nouvelle valeur si non dejà presente
        let swathInfos = {
          numero: element.properties.swathNumber,
          sens: element.properties.orbitDirection.slice(0, 1),
          nbImages: 1, // Compte le nb d'images de cette fauchée, sera rempli après avoir calculé toutes les orbites relatives
          seeOrbiteRelative: false
        }
        let trouve = swathsDispoTab.find(function (elt) {
          return ((elt.numero === swathInfos.numero) && (elt.sens === swathInfos.sens))
        })
        if (trouve === undefined) {
          swathsDispoTab.push(swathInfos)
        } else { // On a déja cette fauchée, on incremente le compteur des images
          trouve.nbImages = trouve.nbImages + 1
        }
      }
    }
  }
</script>

<style scoped>
