<template>
  <div>
    <div id="content">
      <h2>Détails de la Tâche {{id}}</h2>
      <div id="listImages" v-if="variables.length > 0">
          <md-theme md-name="table" md-theme="blue">
          <md-table>
            <md-table-header>
              <md-table-row>
               <md-table-head md-sort-by="process">Nom Variable</md-table-head>
               <md-table-head>Type Variable</md-table-head>
               <md-table-head>Valeur</md-table-head>
             </md-table-row>
             </md-table-header>
             <md-table-body>
               <md-table-row v-for="(item, index) in variables" :key="index">
                <md-table-cell>{{item.varName}}</md-table-cell>
                <md-table-cell>{{item.type}}</md-table-cell>
                <md-table-cell>{{item.value}}</md-table-cell>
               </md-table-row>
             </md-table-body>
          </md-table>
          </md-theme>
      </div>
      <button name="button" v-on:click="sendMessage">Forcer l'avance</button>
     </div>
  </div>
</template>

<script>
  import VueResource from 'vue-resource'
  let moment = require('moment')

  export default {
    name: 'Task',
    components: {
      VueResource
    },
    data () {
      return {
        moment: moment,
        id: '',
        taskInstance: '',
        variables: [],
        wsStatus: '',
        processToken: ''
      }
    },
    mounted: function () {
      this.id = this.$route.params.id
      this.getTaskInstance()
      this.getTaskInstanceVariables()
    },
    watch: {
      $route: function () {
        this.id = this.$route.params.id
        this.getTaskInstance()
        this.getTaskInstanceVariables()
      }
    },
    methods: {
      getTaskInstance () {
        this.$http.get(process.env.PROCESS_URL + ':' + process.env.PROCESS_PORT + '/rest/task/' + this.id, {
          headers: {
            'Accept': 'application/json'
          }
        })
        .then((response) => {
          console.log(response)
          this.taskInstance = response.data
        })
        .catch(function (data, status, request) {
          console.log(data)
        })
      },
      getTaskInstanceVariables () {
        this.$http.get(process.env.PROCESS_URL + ':' + process.env.PROCESS_PORT + '/rest/task/' + this.id + '/variable', {
          headers: {
            'Accept': 'application/json'
          }
        })
        .then((response) => {
          console.log(response)
          this.wsStatus = response.body
          let processToken = ''
          let vars = []
          let keys = Object.keys(response.data)
          keys.forEach(function (element, index, array) {
            let tempVar = response.data[element]
            tempVar.varName = element
            if (element === 'processToken') {
              processToken = response.data[element].value
              console.log(processToken)
            }
            vars.push(tempVar)
          })
          this.processToken = processToken
          this.variables = vars
        })
        .catch(function (data, status, request) {
          console.log(data)
        })
      },
      sendMessage () {
        let message = '{"JobID":"' + this.wsStatus.jobId.value + '", "processToken": "' + this.wsStatus.processToken.value + '", "Progress": "' + this.wsStatus.progress.value + '", "Status": "' + this.wsStatus.status.value + '"}'
        this.$http.post(process.env.PROCESS_URL + ':' + process.env.PROCESS_PORT + '/rest/task/' + this.id + '/message', message, {
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
          }
        })
        .then((response) => {
          console.log(response)
        })
        .catch(function (data, status, request) {
          console.log(data)
        })
      }
    }
  }
</script>



