<template>
  <div>
    <div style="width:100%;text-align:center"><router-link class="link" to="/interferogramme" >Choisir ses données</router-link></div>
    <!-- lister process -->
    <div id="content">
      <h2>Definitions de process</h2>
	<div id="listImages" v-if="process.length > 0">
          <md-theme md-name="table" md-theme="blue">
          <md-table v-once>
            <md-table-header>
              <md-table-row>
               <md-table-head md-sort-by="process">Process Id</md-table-head>
               <md-table-head>Process Key</md-table-head>
               <md-table-head>Process Version</md-table-head>
               <md-table-head>Image</md-table-head>
               <md-table-head>Lancer</md-table-head>
             </md-table-row>
             </md-table-header>
             <md-table-body>
               <md-table-row v-for="(item, index) in process" :key="index">
                <md-table-cell>{{item.id}}</md-table-cell>
                <md-table-cell>{{item.key}}</md-table-cell>
                <md-table-cell>{{item.version}}</md-table-cell>
                <md-table-cell>
                   <div :id="'viewProcess' + index" ></div>
                   <button name="button" v-on:click="getImage(item.id, '#process-images')">Image du process</button>
                </md-table-cell>
                <md-table-cell>
                  <router-link v-if="item.key === 'interferogramme-main'" class="link" to="/interferogramme">{{item.key}}</router-link>
                </md-table-cell>
               </md-table-row>
             </md-table-body>
          </md-table>
          </md-theme>
        </div>
        <div id="process-images"></div>
    </div>
  </div>
</template>

<script>
  let BpmnViewer = require('bpmn-js')
  import Vue from 'vue'
  import VueResource from 'vue-resource'
  import VueMaterial from 'vue-material'

  Vue.use(VueMaterial)

  Vue.material.registerTheme({
    default: {
      primary: 'blue',
      accent: 'black'
    },
    blue: {
      primary: 'blue',
      accent: 'red'
    }
  })

  export default {
    name: 'ListProcess',
    components: {
      VueResource,
      VueMaterial
    },
    data () {
      return {
        process: [],
        diagrams: [],
        processImageDiv: '',
        viewer: ''
      }
    },
    mounted: function () {
      this.getProcess()
      let viewer = new BpmnViewer({container: '#process-images', height: 600})
      this.viewer = viewer
    },
    methods: {
      getProcess () {
        this.$http.get(process.env.PROCESS_URL + ':' + process.env.PROCESS_PORT + '/rest/process/', {
          headers: {
            'Accept': 'application/json'
          }
        })
        .then((response) => {
          this.process = response.data
        })
        .catch(function (data, status, request) {
          console.log(data)
        })
      },
      getImage (id, htmlDiv) {
        this.processImageDiv = htmlDiv
        this.$http.get(process.env.PROCESS_URL + ':' + process.env.PROCESS_PORT + '/rest/process/' + id + '/bpmn', {
          headers: {
            'Accept': 'application/xml'
          }
        })
        .then(function (response) {
          let viewer = this.viewer
          viewer.clear()
          viewer.importXML(response.body, function (err) {
            if (err) {
              console.log('error rendering', err)
            } else {
              viewer.get('canvas').zoom('fit-viewport')
            }
          })
        })
        .catch(function (data, status, request) {
          console.log(data)
        })
      }
    }
  }
</script>

<style>
.md-ink-ripple {
  display: none !important;
}
</style>
<style scoped>
@import url(http://fonts.googleapis.com/css?family=Roboto:300,400,500,700,400italic)
@import url(http://fonts.googleapis.com/icon?family=Material+Icons)

@import "../../node_modules/vue-material/dist/vue-material.css"

svg:not(.md-image){
    height: 600px;
}

.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: table;
  transition: opacity .3s ease;
}

</style>



